import { doLogin, doLogout} from '../../api/sysUser'
import { getToken, setToken, removeToken } from '../../utils/auth';
export default {
  namespaced:true,
  state : {
    isLogin:false,
    sysUserInfo:{},
    menuItem:[]
  },
  mutations : {
    ["SET_SYSUSERINFO"](state,sysUser) {
      console.log(sysUser);
      state.sysUserInfo = sysUser
    },
    ["SET_ISLOGIN"](state,IsLogin){
      state.isLogin = IsLogin
    }
  },
  actions :{
    login( conText, loginInfo) {
      const { username, password } = loginInfo
      return new Promise((resolve, reject) => {
        doLogin({ username: username.trim(), password: password }).then(response => {
          const { obj,code,msg } = response;
          if (code==200) {
            conText.commit("SET_SYSUSERINFO", obj);
            conText.commit("SET_ISLOGIN", true);
            localStorage.setItem("token",obj.CurToken);
            setToken(obj.CurToken);
            resolve();
          } else {
            conText.commit('set_IsLogin', false);
            reject(msg);
          }
        }).catch(error => {
          conText.commit('set_IsLogin', false);
          reject(error)
        })
      })
    },

    logout( conText, state, dispatch ) {
      return new Promise((resolve, reject) => {
        doLogout(state.token).then(() => {
          conText.commit('SET_ISLOGIN', false);
          removeToken();
          resolve();
        }).catch(error => {
          reject(error)
        })
      })
    },
  }
}



